<template>
  <div style="text-align: center">
    <div>
      <span></span>
      <input type="text" v-model="features1" size="25" placeholder="" />
    </div>
    <div>
      <span></span>
      <input type="number" v-model="topn" />
    </div>
    <button @click="predict()">predict</button>
    <button @click="view_proba()">probability</button>
    <div></div>
    <div>{{ this.features1 }}</div>
    <p>{{ result }}</p>
    <br />
    <div></div>
    <div v-for="(result, key) in results" :key="key">
      <b>{{ corr[key] }}</b>
      <br />
      {{ result }}
    </div>
  </div>
</template>

<script>
import IrisDataset from "ml-dataset-iris";
import { RandomForestClassifier as RFClassifier } from "ml-random-forest";
import { RandomForestRegression as RFRegression } from "ml-random-forest";
import dataset from "./dataset";
import search_label from "./search_by_idf";
import axios from "axios";

export default {
  data() {
    return {
      features1: "",
      result: "",
      result2: "",
      corr: "",
      results: [],
      topn: 1
    };
  },
  methods: {
    predict() {
      this.results = [];
      let res = search_label(this.features1);
      let labels = [];
      for (let i = 0; i < res.length; i++) {
        labels.push(res[i][0]);
      }
      this.result = labels;

      const payload = {
        text: this.features1,
        topn: parseInt(this.topn)
      };
      const url = "http://13.115.157.181/v1/api/";
      let word;
      let res2;
      axios
        .post(url, payload)
        .then(response => {
          this.corr = response.data.sim;
          for (let i = 0; i < this.corr.length; i++) {
            let cor = this.corr[i];
            res2 = search_label(cor);
            let labels2 = [];
            for (let i = 0; i < res2.length; i++) {
              labels2.push(res2[i][0]);
            }
            this.results.push(labels2);
          }
        })
        .catch(err => {});
    },
    view_proba() {
      let res = search_label(this.features1);
      let labels = [];
      for (let i = 0; i < res.length; i++) {
        labels.push(res[i]);
      }
      this.result = labels;

      const payload = {
        text: this.features1
      };
      const url = "http://127.0.0.1:8000/v1/api/";
      let word;
      let res2;
      axios
        .post(url, payload)
        .then(response => {
          console.log(response.data);
          word = response.data.sim;
          res2 = search_label(word);
          let labels2 = [];
          for (let i = 0; i < res2.length; i++) {
            labels2.push(res2[i]);
          }
          this.result2 = labels2;
        })
        .catch(err => {});
    },
    request_similar_word(callback) {
      const payload = {
        text: this.features1
      };
      const url = "http://127.0.0.1:8000/v1/api/";
      axios
        .post(url, payload)
        .then(response => {
          console.log(response.data);
          return response.data.sim;
        })
        .catch(err => {});
    }
  }
};
</script>
